html {
  font-size: 62.5%;
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto; }

.padding {
  padding: 3rem; }

.seccion {
  padding: 3rem; }

p {
  margin: 0;
  padding: 0; }

section {
  padding: 3rem; }

img,
picture {
  width: 100%;
  display: block; }

a {
  text-decoration: none;
  font-size: 2rem; }

h1 {
  font-size: 3.8rem; }

h2 {
  font-size: 3.4rem; }

h3 {
  font-size: 3rem; }

h4 {
  font-size: 2.6rem; }

.error {
  margin: 0 auto;
  max-width: 50%;
  text-align: center;
  background-color: #F8D7D9;
  color: #721C24;
  padding: .5rem 0;
  font-size: 2rem;
  margin-bottom: 1rem;
  border-radius: 5px; }

.mensaje {
  background-color: #CDE5FE;
  color: #004184;
  padding: 2rem;
  text-align: center;
  max-width: 50%;
  margin: 0 auto;
  font-size: 2rem;
  border-radius: 1rem; }

.centrar {
  text-align: center; }

.btn-large {
  display: grid; }

.btn {
  color: white;
  padding: 1rem 1.2rem;
  border-radius: 5px; }

.info {
  background-color: #0d9bb7; }

.correct {
  background-color: #12623c; }

