/** Media Queries **/
.contenedor {
  text-align: center; }

.temas {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 5rem; }
  .temas .tema {
    -webkit-box-shadow: 1px 1px 15px 3px rgba(0, 0, 0, 0.46);
    box-shadow: 1px 1px 15px 3px rgba(0, 0, 0, 0.46);
    padding: 2rem; }
    .temas .tema h4 {
      color: white; }
    .temas .tema p {
      font-size: 1.8rem;
      margin: 2rem 0; }

.tituloImagen {
  position: relative;
  display: inline-block; }
  .tituloImagen img {
    height: 200px;
    width: 300px; }
  .tituloImagen .img:before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(24, 23, 23, 0.7); }
  .tituloImagen .centrado {
    width: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); }

nav {
  display: grid;
  grid-template-columns: 75% 20% 0%;
  gap: 2rem;
  text-align: center;
  background-color: #212529;
  padding: 2rem; }
  nav .enlaces {
    display: grid;
    grid-template-columns: repeat(12, 1fr); }
    nav .enlaces a {
      font-size: 1.6rem;
      color: #c8c8c8;
      border-bottom: 1px solid transparent;
      margin-bottom: -1.5rem; }
    nav .enlaces :hover {
      color: white;
      border-bottom: 1.5px solid white;
      transition: all ease-out .3s; }
  nav .login {
    display: grid;
    grid-template-columns: repeat(3, 1fr); }
    nav .login a {
      font-size: 1.8rem;
      color: white; }
  nav .menu {
    color: white;
    font-size: 2rem; }

form {
  max-width: 50%;
  margin: 0 auto; }
  form fieldset {
    -webkit-box-shadow: 1px 1px 15px 3px rgba(0, 0, 0, 0.46);
    box-shadow: 1px 1px 15px 3px rgba(0, 0, 0, 0.46);
    padding: 2rem; }
  form label {
    font-size: 1.8rem; }
  form input {
    width: 100%;
    margin: .7rem 0;
    font-size: 2rem;
    border: 1px solid #817f7f;
    border-radius: 5px; }
  form textarea {
    margin: .7rem 0;
    font-size: 2rem;
    border: 1px solid #817f7f;
    width: 100%; }

.view .encabezado {
  text-align: left;
  color: #020293; }

.view p {
  font-size: 1.8rem;
  word-break: break-all; }
  .view p img {
    width: 50%;
    height: auto; }

.documentos {
  border: solid 1px rgba(0, 0, 0, 0.1);
  background-color: rgba(205, 205, 205, 0.1);
  border-radius: 1rem;
  align-items: center;
  grid-template-columns: 80% 20%;
  display: grid; }
  .documentos img {
    width: 20%; }
  .documentos a {
    font-size: 10rem; }
    .documentos a i {
      color: #02551f; }
      .documentos a i p {
        margin-top: 2rem; }
